



@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/OpenSans-Regular.ttf.eot?#iefix') format('embedded-opentype'),  url('../fonts/OpenSans-Regular.ttf.woff') format('woff'), url('../fonts/OpenSans-Regular.ttf.ttf')  format('truetype'), url('../fonts/OpenSans-Regular.ttf.svg#OpenSans-Regular.ttf') format('svg');
  font-weight: normal;
  font-style: normal;
}

html, body{ background: rgb(248, 247, 247); height: 100%; margin: 0; padding: 0; color: #3d3d3d; font: 14px Arial, Helvetica, sans-serif;}
body{position: relative; cursor: default;}
img{max-width: 100%;}
a img {border: 0 none;}
h1, h2, h3 ,
h4, h5, h6{padding: 0; margin: 1.2em 0 0.8em; text-transform: uppercase; font-weight: 100}
article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary{display: block;}
a{text-decoration: none; color: #232323;}
a:hover{text-decoration: underline;}
a span, a font{cursor: pointer;}
hr{background: #3e76b3; height: 1px; border: 0 none; margin: 20px 0;}
label{cursor: pointer; -moz-user-select: none; -khtml-user-select: none; user-select: none;}
input[type="submit"], button{cursor: pointer;}
textarea{resize: vertical;}
input,
textarea,
button{font-family: inherit; color: inherit; outline: 0 none; box-sizing: border-box;}
button::-moz-focus-inner{padding: 0; border: 0;}
::selection{color: #222; background: #ccd6eb;}
::-moz-selection{color: #222; background: #ccd6eb;}
table {border-collapse: collapse; border-spacing: 0;border: 0;}
table td{vertical-align: top;}
