/*Popup*/

.overflow-hidden {
  overflow:hidden;
}

.popup {
  display: block;
  opacity: 0;
  visibility: hidden;
  transition: opacity .5s , top .5s;
  overflow: auto;
  position: fixed;
  left: 0;
  right: 0;
  z-index: 999999;
  bottom: 0;
  top: 0;



}

.popup__wp {
  max-width: 736px;
  background: #fff;
  box-sizing: border-box;
  margin:85px auto 85px;
  box-shadow: 0 1px 9px rgba(30,15,0,.53);
}



.overlay:target {
  visibility: visible;
  opacity: 1; }

.overlay:target + .popup {
  opacity: 1;
  visibility: visible; }

.overlay {
  visibility: hidden;
  background: rgba(80, 80, 80, 0.5);
  bottom: 0;
  opacity: 0;
  z-index: 99999;
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
  cursor: default;
  position: fixed;
  top: 0;
  right: 0;
  left: 0; }

.popup__top {
  text-align: right;
  height: 60px;

}

.popup__content .title {
  font-weight: bold;
}

.popup__text {
  margin-bottom: 28px; }

.popup__content {
  padding: 0 50px 50px 50px;
  font-size: 15px;
}

.icon-close {
  display: inline-block;
  text-decoration: none;
  margin: 25px;
  height: 18px;
  width: 18px;
  background: url("../img/icon-close.png") no-repeat;
}

.icon-close:hover {

  }

/*Popup end*/

/******************form*********************/

.form__form {
  margin-top: 30px;
}

.form__row {
  margin: 10px 0 11px 0px;
  position: relative;
}

.form__label {
  display: inline-block;
  vertical-align: top;
  width: 227px;
  font-size: 16px;
  margin-top: 11px;
}



.form__input {
  box-sizing: border-box;
  border: 1px solid black;
  width: 295px;
  height: 34px;
  line-height: 34px;
  font-size: 16px;
  font-family: 'PT Sans';
  padding: 0 10px;
  background: white;


}

.form__input:hover,.form__input:focus {
  outline: none;
}

.CHANGED.REQUIRED+.input__valid {
  display: block;
}

.CHANGED.REQUIRED {

}

.btn2-active {

}


.input__valid {
  display: none;
  text-transform: uppercase;
  font-weight: 500;
  padding: 5px 22px 0 17px;
  width: 250px;
  letter-spacing: 1px;
}

.form__textarea {
  height: 240px;
}

.form__submit {
  font-size: 20px;
  display: block;
  margin: 0 auto;
  margin-top: 36px;
  transition: background .3s;
}

.form__file {
  font-size: 16px;
  text-decoration: underline;
  color: black;
  text-transform: uppercase;
  font-weight: 700;
  font-family: 'PT Sans';
  cursor: pointer;
}

.jq-selectbox__trigger-arrow {
  position: absolute;
  top: 12px;
  right: 8px;
  width: 16px;
  height: 9px;
  cursor: pointer;
  background: url("../img/icon-trigger.png") no-repeat;
}

.jq-selectbox__select-text {
  overflow: hidden;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 30px;
  box-sizing: border-box;
}

.jq-selectbox__dropdown {
  width: 295px;
  margin-top: -2px;
  left: -1px;
  background: white;
  border: 1px solid black;

  box-sizing: border-box;
}

.jq-selectbox__dropdown .sel {
  background: #EBEBEB;
}

.jq-selectbox__dropdown ul {
  margin: 0;
}

.jq-selectbox li {
  border-bottom: 1px solid #ccccce;
  cursor: pointer;
  padding: 5px 10px;
  line-height: 1.2;
}


.jq-file__name {
  float: right;
  text-transform: none;
  font-weight: 400;
}

.jq-file__name:hover {
  text-decoration: none;
}

.jq-file__browse {
  cursor: pointer;
  color: black;
  text-decoration: underline;
  padding: 2px 0;
  cursor: pointer;


}
.form__file, .input-file, .items__more, .jq-file, .jq-file input, .jq-file__browse{
  cursor: pointer;
}

.text-right {
  text-align: right;
}

.form__file {
  width: 345px;
  margin-top: 12px;
}


.jq-radio {
  top: -1px;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  margin: 0 10px 0 0;
  border: 1px solid black;
  border-radius: 50%;

  cursor: pointer;
}
.jq-radio.checked .jq-radio__div {
  width: 8px;
  height: 8px;
  margin: 4px 0 0 4px;
  border-radius: 50%;
  background: #fe981b;

}

.jq-radio.disabled {
  opacity: .55;
}

.radios label {
  margin-right: 60px;
  font-family: 'PT Sans';

}

.radios {
  margin: 34px 0;
}




/******************form END*****************/
/*Slider*/

.slider-main {
  height: 360px;
  overflow: hidden;
  position: relative;
}


.slider-main__block {
  position: relative;
  background: no-repeat;
  background-size: cover;
  height: 360px;
  width: 100%;
}


.slider-main__text {
  color: #4e5366;
  font-size: 18px;
}

.slider-main__title {
  color:black;
  text-transform: uppercase;
  font-size: 35px;
  margin-bottom: 28px;
}

.slider-main__content {
  padding-left: 105px;
  padding-top: 120px;
}

.slider-main img {
  height: 100%;
}

.slick-slider {
  margin-bottom: 0;
}
.slick-slide img {
  margin: 0 auto;
}

.slick-dots {
  bottom: -5px;
}

.slick-dots li button:before {
  font-size: 32px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: 'â€¢';
  text-align: center;
  color: black;
  opacity: 1;
}

.slick-dots li.slick-active button:before {
  color: red;
  opacity: 1;
}

.slick-dots li button:focus:before, .slick-dots li button:hover:before {
  opacity: 1;
}

.slick-dots li {
  margin: 0;
}
.slick-dots li, .slick-dots li button {
  width: 19px;

}



.slider-main .slick-prev {
  left: 24px;
  z-index: 1;
}

.slider-main .slick-next {
  right: 24px;
  z-index: 1;
}




.slick-next:before, .slick-prev:before {
  content:'';
    width: 20px;
    height: 33px;
    background: url("../img/icon-arrow-left.png") no-repeat;
    z-index: 1;
    display: block;
}

.slick-next:before  {
  transform: rotate(180deg);
}



/*Slider-item*/
  .slider-item__item {
    width: 215px;
    display: block;
    text-decoration: none;
  }
  .slider-item__img {
    border-radius: 3px;
  }

  .slider-item__title {
